.container {
  max-width: 100%;
  padding: 0 20px;
}

@media screen and (min-width: 380px) {
  .container {
    max-width: 380px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    margin: 0 auto;
    padding: 0 75px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
    margin: 0 auto;
    padding: 0 71px;
  }
}

body {
  box-sizing: border-box;
  color: #000;
  background-color: #fff;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

li {
  list-style: none;
}

